import { createRouter, createWebHistory } from 'vue-router';

import coachesList from './pages/coachesList.vue';
import contactCoaches from './pages/contactCoaches.vue';
import notFound from './pages/notFound.vue';
import RegisterCoaches from './pages/RegisterCoaches.vue';
import RequestsReceived from './pages/RequestsReceived.vue';
import CoachDetail from './pages/CoachDetail.vue';

const router = createRouter({
  history: createWebHistory(),
  routes: [
    { path: '/', redirect: '/coaches' },
    { path: '/coaches', component: coachesList },
    { path: '/coaches/:id', props: true, component: CoachDetail, children: [
      {path: 'contact', component: contactCoaches},
    ] },
    { path: '/register', component: RegisterCoaches },
    { path: '/requests', component: RequestsReceived },
    { path: '/:notFound(.*)', component: notFound },
  ],
});

export default router;
