<template>
	<div>
	<base-dialog :show="!!error" title="An error occured!" @close="handleError">
		<p>{{ error }}</p>
	</base-dialog>
	<section>
		<coach-filter @change-filter="setFilters"></coach-filter>
	</section>
	<section>
		<base-card>
		<div class="controls">
			<base-button mode="outline" @click="loadCoaches(true)">Refresh</base-button>
			<!-- <router-link to="/coaches">Refresh</router-link> -->
			<br>
			<base-button v-if="!isCoach && !isLoading" link to="/register">Register</base-button>
		</div>
		<div v-if="isLoading">
			<base-spinner></base-spinner>
		</div>
		<ul v-else-if="hasCoaches">
			<coach-item v-for="coach in filteredCoaches"
				:key="coach.id"
				:id="coach.id"
				:first-name="coach.firstName"
				:last-name="coach.lastName"
				:rate="coach.hourlyRate"
				:areas="coach.areas"
				></coach-item>
		</ul>
		<h3 v-else>No coaches womp womp</h3>
		</base-card>
	</section>
	</div>
</template>


<script>
import CoachItem from '../components/coaches/CoachItem.vue';
import CoachFilter from '../components/coaches/CoachFilter.vue';

export default {
	components: {
		CoachItem,
		CoachFilter
	},
	computed: {
		filteredCoaches() {
			const coaches = this.$store.getters['coaches/coaches'];
			return coaches.filter(coach => {
				if (this.activeFilters.frontend && coach.areas.includes('frontend')) {
					return true;
				}
				if (this.activeFilters.backend && coach.areas.includes('backend')) {
					return true;
				}
				if (this.activeFilters.career && coach.areas.includes('career')) {
					return true;
				}
					return false;
			});
		},
		hasCoaches() {
			return !this.isLoading && this.$store.getters['coaches/hasCoaches'];
		},
		isCoach() {
			return this.$store.getters['coaches/isCoach'];
		}
	},
	data() {
		return {
			activeFilters: {
				frontend: true,
				backend: true,
				career: true
			},
			isLoading: false,
			error: null,
		}
	},
	created() {
		this.loadCoaches();
	},
	methods: {
		setFilters(updatedFilters) {
			this.activeFilters = updatedFilters;
		},
		handleError() {
			this.error = null;
		},
		async loadCoaches(refresh = false) {
			this.isLoading = true;
				try {
					await this.$store.dispatch('coaches/loadCoaches', {forceRefresh: refresh});
				} catch(error) {
					this.error = error.message || 'Something went wrong!';
					console.log(error)
				}
			this.isLoading = false
		},

	/* plzClap() { */
	/* 	console.log(`career is set to ${this.activeFilters.career}`); */
	/* } */
  }
}

</script>

<style>
ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.controls {
  display: flex;
  justify-content: space-between;
}
</style>
